import * as React from 'react';
import {styles} from './webgl.module.scss';
import {useEffect} from "react";
import {BackBtn} from "../../../../components/backBtn";
import {ROUTES} from "../../../../config";

export const Webgl = () => {
  const isBrowser = typeof window !== "undefined"
  useEffect(()=>{
    if(isBrowser&& !window.webjs3d  ){
      window.webjs3d=true
      const lab =  document.createElement('script');
      const config = document.createElement('script');
      const folder = '/gallery/webgl-fluid/';
      lab.src = folder+'dat.gui.min.js';
      config.src= folder+ 'config.js';
      document.body.append(lab);
      lab.addEventListener('load', ()=>{
        document.body.append(config);
      })
    }
    }, []
  )

  return  <div className={styles}>
    <BackBtn external   link={ROUTES.gallery}/>
    <canvas/></div>
}

